import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { createUser,validUser,signOutUser,checkAuth} from './authAPI';
import { updateUser } from '../user/userAPI';
import { notify } from '../common/notify';

const initialState = {
  loggedInUser: null,
  status: 'idle',
  error:null,
  userChecked:false
};

export const createUserAsync = createAsyncThunk(
  'user/createUser',
  async (userData) => {
    const response = await createUser(userData);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const checkAuthAsync =createAsyncThunk(
  'user/checkAuth',
  async () => {
    try{
      const response = await checkAuth();
      // The value we return becomes the `fulfilled` action payload
      return response.data;
    }
    catch(error){
      console.log(error)
      return error;
        // return rejectWithValue(error);
    }
  }
);

export const signOutUserAsync = createAsyncThunk(
  'user/signOutUser',
  async (userData) => {
    const response = await signOutUser(userData);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const updateUserAsync = createAsyncThunk(
  'user/updateUser',
  async (update) => {
    const response = await updateUser(update);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const validUserAsync = createAsyncThunk(
  'user/validUser',
  async (userData,{rejectWithValue}) => {
    try{
      const response = await validUser(userData);
      // The value we return becomes the `fulfilled` action payload
      return response.data;
    }
    catch(error){
      console.log(error)
        return rejectWithValue(error);
    }

  }
);


export const authSlice = createSlice({
  name: 'user',
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    increment: (state) => {
      state.value += 1;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(createUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(createUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.error=null;
        state.loggedInUser = action.payload;
        notify.success(action.payload.fullName + ' Created account Successfully');
      })
      .addCase(createUserAsync.rejected, (state, action) => {
        state.status = 'idle';
        state.loggedInUser = null;
        state.error=action.error;
        notify.error(action.error.message);
      })
      .addCase(validUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(validUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.loggedInUser = action.payload;
        state.error=null;
        notify.success(state.loggedInUser.fullName+" Login Success");
      })
      .addCase(validUserAsync.rejected, (state, action) => {
        console.log(action)
        state.status = 'idle';
        state.error = action.payload;
        notify.error(action.payload.message);
      })
      .addCase(updateUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(updateUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.loggedInUser = action.payload;
      })
      .addCase(signOutUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(signOutUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.loggedInUser = null;
        notify.success("User signed out");
      })
      .addCase(checkAuthAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(checkAuthAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.loggedInUser = action.payload;
        state.error=null;
        state.userChecked=true;
        notify.success(state.loggedInUser.fullName+" Login Success");
      })
      .addCase(checkAuthAsync.rejected, (state, action) => {
        state.status = 'idle';
        state.error = action.payload;
        state.userChecked=true;
        notify.error(action.payload.message);
      })
      
  }
});

export const { increment } = authSlice.actions;

export const selectLoggedInUser = (state) => state.auth.loggedInUser;

export const selectError = (state) => state.auth.error;
export const selectAuthStatus = (state) => state.auth.status;
export const selectUserChecked =(state)=> state.auth.userChecked;
export default authSlice.reducer;
