// A mock function to mimic making an async request for data
import { configUrl } from "../../app/contants";
export function createUser(userData) {
  return new Promise(async(resolve,reject) =>{
    const response= await fetch(`${configUrl.url}/auth/signup`,{
      method:"POSt",
      body:JSON.stringify(userData),
      headers:{'content-type':'application/json'}
    });
    if(response.ok){
      const data = await response.json();
      resolve({data});
    }
    else{
      const error = await response.json();
      reject(error);
    }
    
});
}
export function checkAuth(){
  return new Promise(async(resolve,reject) =>{
      try{
        const response= await fetch(`${configUrl.url}/auth/checkAuth`,);
        if(response.ok){
          const data = await response.json();
          resolve({data});
        }
        else{
          const error = await response.json();
          reject(error);
        }
      }
      catch(error){
        
      }
  })
}
export function validUser(userData) {
  return new Promise(async(resolve,reject) =>{
    try{
      const response= await fetch(`${configUrl.url}/auth/login`,{
        method:"POSt",
        body:JSON.stringify(userData),
        headers:{'content-type':'application/json'}
      });
      if(response.ok){
        const data = await response.json();
        resolve({data});
      }
      else{
        const error = await response.json();
        reject(error);
      }
    }
    catch(er){
      reject({er})
    }
    
    
});
}

